 .colorPickerOption {
   display: inline-block;
   width: 40px;
   height: 40px;
   margin: 4px;
   border: none;
   outline: none;
   cursor: pointer;
   transition: transform 250ms linear;
 }
 
 .colorPickerOption--active {
   transform: scale(1.2);
 }
 